import alt from '../alt';
import MillActions from '../actions/MillActions';

class MillStore {
    constructor() {
        this.mills = [];

        this.bindListeners({
           handleUpdateMills: MillActions.UPDATE_MILLS
        });
    }

    handleUpdateMills(mills) {
        this.mills = mills;
    }
}

export default alt.createStore(MillStore, 'MillStore');


